.Info {
    position: fixed;
    bottom: 0px;
    height: 30mm;
    width: 100%;
    background-color: rgb(46, 46, 46);
    text-align: left;
    overflow: hidden;
    opacity: 0;
    animation: toTransparent 300ms;
}

@keyframes toTransparent {
    from {
        opacity: 100%;
    }

    to {
        opacity: 0%;
    }
}

@keyframes toVisible {
    from {
        opacity: 0%;
    }

    to {
        opacity: 100%;
    }
}

.Info:hover {
    animation: toVisible 300ms;

    opacity: 100%;
}
  
.Info p {
    color: white;
    margin-left: 3mm;
}