@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');

* {
  font-family: 'Quicksand', sans-serif;

}

.content {
  margin-top: 50px;
  height:fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.content p {
  margin-bottom: 10mm;
}

.Image {
  width: 20vw;
  border-radius: 2vw;
  margin-bottom: 10mm;
  margin-right: 10mm;
  margin-left: 10mm;
  transition: border-radius 100ms ease-out 100ms;
}

.Image:hover {
  border-radius: 1vw;
}

.UnderTitle {
  font-size: 5mm;
  transition: font-size 50ms ease 50ms;
}

.UnderTitle:hover {
  font-size: 6mm;
}


.Title {
  font-size: 4vw;
  margin-bottom: 7mm;
  margin-top: 0;
  transition: font-size 50ms ease 50ms;
}

.Title:hover {
  font-size: 4.5vw;
}

.images {
  display: flex;
  flex-direction: row;
}

.GameRow {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
  width: 95vw;
  justify-content: center;
  align-items: center;
  margin-bottom: 1vw;
}


body {
  background-color: #ffef8a;
}

.notfound {
  width: fit-content;
  height: fit-content;
  text-align: center;
  border-radius: 15px;
  box-shadow: rgba(100, 100, 111, 0.5) 0px 7px 29px 0px;
}
.notfound h1 {
  margin-left: 40px;
  margin-right: 40px;
  font-family: 'Roboto', sans-serif;

}

.notfound button {
  margin-bottom: 30px;
  width: 30%;
  height: 40px;
  border: none;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 15px;
  background-color: rgb(233, 233, 233);
}

.notfound button:hover {
  cursor: pointer;
}