.GameCard {
    display: flex;
    width: 20vw;
    height: 20vw;
    animation: toSmall 150ms;

    border-radius: 3vw;
    background: linear-gradient(145deg, #ffff94, #e6d77c);
    box-shadow:  23px 23px 38px #736b32,
                -23px -23px 38px #ffff8a;

    align-items: center;
    justify-content: center;
    flex-direction: column;

    margin-left: 1vw;
    margin-right: 1vw;
    overflow: hidden;
}

@keyframes toBig {
    from {
        border-radius: 3vw;
    }

    to {
        border-radius: 2vw;
    }
}

@keyframes toSmall {
    from {
        border-radius: 2vw;
    }

    to {
        border-radius: 3vw;
    }
}

.GameCard:hover {
    cursor: pointer;
    animation: toBig 150ms;
    border-radius: 2vw;
}

.GameCard p {
    max-width: 80%;
    text-align: center;
    font-size: 1vw;
    
}

.GameCard img {
    width: 60%;
    border-radius: 15px;
}

.GameCard h1 {
    margin-bottom: 0;
    font-size: 3vw;
    margin-top: 0;
}

.GameCard p {
    margin-top: 0mm;
    margin-bottom: 4mm;
}