@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');


.navbar {
  position:relative;
  display: flex;
  height: 60px;
  flex-direction: row;
  background-color: rgb(243, 243, 243);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  gap: 15px;
  text-align: center;
  border-radius: 0px;
  background: linear-gradient(145deg, #ffff94, #e6d77c);
  box-shadow:  9px 9px 18px #807845,
              -9px -9px 18px #ffffcf;
}

.navbar ul {
  display: flex;
  flex-direction: row;
  gap: 20px;
  list-style: none;
  justify-content: center;
  align-items: center;
}

.navbar li {
  text-align: center;
  font-size: 20px;
}

.navbar li a {
  text-decoration: none;
  color: black; 
  font-family: 'Roboto', sans-serif;

}

.bumbdev {
  text-decoration: none;
  font-size: 20px;
  font-weight:550;
}
